<!--
@Description: Â∞ÅË£Öelement table selectÁªÑ‰ª∂
@Last-edit-date: 2019.11.16
@Easy o.o <<<<<<<
<ElTableSelect
  :columns="tableColumns"
  :data="tableData"
  :operation="['check']"
  :is-selection="true"
  :page-size="5"
  :total="total"
  :judgeValue="'index'"
  @handleCurrentChange="handleCurrentChange"
  @handleConfirm="handleConfirm"
  @handleCancel="handleCancel"
/>
-->
<template>
  <section class="el-table-container">
    <el-table
      ref="tableGroup"
      :data="data"
      :highlight-current-row="true"
      :row-key="getRowKeys"
      border
      stripe
      size="small"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      @select="handleSelect"
      @select-all="handleSelectAll"
    >
      <!--Â§öÈÄâÂàó-->
      <el-table-column
        v-if="isSelection"
        type="selection"
        align="center"
        width="55"
      />

      <!-- Â±ïÁ§∫Âàó -->
      <template v-for="(column, index) in columns">
        <slot
          v-if="column.slotName"
          :name="column.slotName"
        />
        <component
          :is="column.component"
          v-if="column.component"
          :key="index"
          :params="column.params"
        />
        <el-table-column
          v-if="column.prop"
          :key="index"
          :label="column.label"
          :prop="column.prop"
          :width="column.width"
          :class-name="column.className"
          align="center"
        />
      </template>

      <!-- Êìç‰ΩúÂàó -->
      <el-table-column
        :width="fixedWidth"
        label="Êìç‰Ωú"
        fixed="right"
        align="center"
      >
        <template slot-scope="scope">
          <el-button v-if="operationGroup('edit')" icon="el-icon-edit" type="success" size="mini" @click="handleEdit(scope.$index, scope.row)">
            ÁºñËæë
          </el-button>
          <el-button v-if="operationGroup('insert')" icon="el-icon-plus" size="mini" @click="handleInsert(scope.$index, scope.row)">
            Â¢ûÂä†
          </el-button>
          <el-button v-if="operationGroup('delete')" icon="el-icon-close" type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)">
            Âà†Èô§
          </el-button>
          <el-button v-if="operationGroup('check')" icon="el-icon-info" type="info" size="mini" @click="handleCheck(scope.$index, scope.row)">
            Êü•Áúã
          </el-button>
          <!-- Ëá™ÂÆö‰πâÊåâÈíÆ -->
          <span
            v-if="operationGroup('custom')"
            style="display:inline-block;"
          >
            <!--eslint-disable-->
            <el-button
              v-for="(i,index) in customList"
              v-if="!i.condition || i.condition.value.indexOf(scope.row[i.condition.key]) >= 0"
              :key="index"
              :icon="i.icon"
              :type="i.btnType"
              size="mini"
              @click="handleCustom(scope.$index, scope.row, i.handler)"
            >
              {{ i.buttonName }}
            </el-button>
          </span>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      v-if="isPagination"
      background
      :current-page="currentPage"
      :page-sizes="pageSizes"
      :page-size="pageSize"
      :layout="layout"
      :total="total"
      class="el-pagination-c"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />

    <el-row class="el-row-select">
      <el-col :offset="18" :span="3" class="el-col-select">
        <el-button type="primary" size="small" @click="handleConfirm">Á°ÆÂÆö</el-button>
      </el-col>
      <el-col :span="3" class="el-col-select">
        <el-button size="small" @click="handleCancel">ÂèñÊ∂à</el-button>
      </el-col>
    </el-row>
  </section>
</template>

<script>
export default {
  name: 'ElTableSelect',
  props: {
    /*
    * Ë°®Ê†º
    * */
    // Ë°®Ê†ºÂ±ïÁ§∫Âàó
    columns: {
      type: Array,
      required: true
    },
    // Ë°®Ê†ºÊï∞ÊçÆ
    data: {
      type: Array,
      required: true
    },
    // ÊòØÂê¶ÊòæÁ§∫Â§öÈÄâÂàó
    isSelection: {
      type: Boolean,
      default: false
    },
    // Êìç‰ΩúÁªÑ
    // Ëá™ÂÆö‰πâÊåâÈíÆ{icon: 'ÂõæÊ†á', btnType: 'ÊåâÈíÆÁ±ªÂûã', handler: 'ÊâßË°åÁöÑÊñπÊ≥ï', buttonName: 'ÊåâÈíÆÂêçÂ≠ó',condition: {} ÊòæÁ§∫Âà§Âà´ÂÄº}
    operation: {
      type: Array,
      default: () => []
    },
    // ‰º†ÂÖ•Ëá™ÂÆö‰πâÊåâÈíÆÊó∂ÔºåÈúÄË¶ÅÂª∂Â±ïÂÆΩÂ∫¶ÁöÑÊúÄÂ§ßÂÄº
    operationCustomLength: {
      type: Number,
      default: 0
    },
    // ÂõûÊòæÂà§Âà´ÂÄºÁöÑname
    judgeValue: {
      required: true,
      type: String
    },

    /*
    * ÂàÜÈ°µ
    * */
    // ÊòØÂê¶ÊòæÁ§∫
    isPagination: {
      type: Boolean,
      default: true
    },
    total: {
      type: Number,
      default: 0
    },
    // ÂΩìÂâçÈ°µÈù¢
    currentPage: {
      type: Number,
      default: 1
    },
    // Ëá™ÂÆö‰πâÊØèÈ°µÈù¢Êï∞ÈáèÁªÑ
    pageSizes: {
      type: Array,
      default: () => [10, 20, 50, 100]
    },
    // ÊØèÈ°µÈù¢Êï∞Èáè
    pageSize: {
      type: Number,
      default: 10
    },
    // total Êï∞ÊçÆÊÄªÈáè, sizes ÊØèÈ°µÈù¢Êï∞ÈáèÁªÑ, prev ‰∏ä‰∏ÄÈ°µ, pager, next ‰∏ã‰∏ÄÈ°µ, jumper È°µÈù¢Ë∑≥ËΩ¨
    layout: {
      type: String,
      default: 'prev, pager, next, jumper'
    }
  },
  data() {
    return {
      getRowKeys(row) {
        return row.index
      },
      cacheCurrentPage: 1,
      cacheSelectData: []
    }
  },
  computed: {
    // Êìç‰ΩúÊåâÈíÆÁöÑÂÆΩÂ∫¶Ëá™ÈÄÇÂ∫î
    fixedWidth() {
      return this.operationCustomLength ? this.operationCustomLength * 110 - 5 : this.operation.length * 110 - 5
    }
  },
  mounted() {
    this.cacheCurrentPage = this.currentPage
  },
  methods: {
    /*
    * Ë°®Ê†º
    * */
    // Á≠õÈÄâË°åÁöÑÊìç‰Ωú
    operationGroup(affix) {
      if (affix === 'custom') {
        // Ëá™ÂÆö‰πâÂàóË°®
        this.customList = this.operation.filter(n => {
          return typeof n === 'object'
        })
        return this.customList
      } else {
        // Âõ∫ÂÆö
        return this.operation.indexOf(affix) >= 0
      }
    },
    // Ëá™ÂÆö‰πâ‰∫ã‰ª∂Ëß¶Âèë
    handleCustom(index, row, handler) {
      this.$emit(handler, index, row)
    },
    // ÁºñËæë‰∫ã‰ª∂
    handleEdit(index, row) {
      this.$emit('handleEdit', index, row)
    },
    // ÊèíÂÖ•‰∫ã‰ª∂
    handleInsert(index, row) {
      this.$emit('handleInsert', index, row)
    },
    // Âà†Èô§‰∫ã‰ª∂
    handleDelete(index, row) {
      this.$confirm('Á°ÆËÆ§Âà†Èô§?', 'ÊèêÁ§∫', {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      }).then(() => {
        this.$emit('handleDelete', index, row)
      })
    },
    // Êü•Áúã‰∫ã‰ª∂
    handleCheck(index, row) {
      this.$emit('handleCheck', index, row)
    },
    // ÈÄâ‰∏≠ÊîπÂèò‰∫ã‰ª∂
    handleSelectionChange(val) {
      this.$emit('handleSelectionChange', val)
    },
    // Âçï‰∏™ÈÄâ‰∏≠‰∫ã‰ª∂
    handleSelect(list) {
      this.cacheSelectData[this.cacheCurrentPage] = list
      this.$emit('handleSelect', list)
    },
    // ÂÖ®ÈÄâ‰∫ã‰ª∂
    handleSelectAll(list) {
      // Èò≤Ê≠¢ÊåáÈíàÈîô‰π±
      this.cacheSelectData[this.cacheCurrentPage] = list.length ? list : null
      this.$emit('handleSelect', list)
    },
    // ÂçïË°åÈÄâ‰∏≠
    setSelection(row, bool) {
      this.$refs.tableGroup.toggleRowSelection(row, bool)
    },
    // Ê∏ÖÁ©∫ÈÄâ‰∏≠
    clearSelection() {
      this.$refs.tableGroup.clearSelection()
    },
    // ÂàÜÈ°µÈÄâÊã©ÂõûÊòæ
    echoSelection(currentPageSelectList) {
      if (currentPageSelectList) {
        const _list = this.data.filter(row => {
          return currentPageSelectList.some(item => {
            return item[this.judgeValue] === row[this.judgeValue]
          })
        })
        _list.forEach(row => {
          this.setSelection(row)
          // or
          // this.$refs.tableGroup.toggleRowSelection(row)
        })
      }
    },

    /*
    * ÂàÜÈ°µ
    * */
    handleSizeChange(num) {
      this.$emit('handleSizeChange', num)
    },
    handleCurrentChange(page) {
      this.$emit('handleCurrentChange', page)
      this.cacheCurrentPage = page
      // Á≠âÂæÖË°®Ê†ºÊï∞ÊçÆÂõûÊòæ
      setTimeout(() => {
        this.echoSelection(this.cacheSelectData[page])
      }, 500)
    },

    /*
    * ÊåâÈíÆ
    * */
    // Á°ÆÂÆö
    handleConfirm() {
      // ÊâÄÊúâÈÄâ‰∏≠ÁöÑÊï∞ÊçÆ
      const _AllSelectData = [].concat(...this.cacheSelectData).filter((item) => {
        return item !== null && typeof item === 'object'
      })
      this.$emit('handleConfirm', _AllSelectData)
      this.cacheSelectData = []
      this.clearSelection()
    },
    // ÂèñÊ∂à
    handleCancel() {
      this.cacheSelectData = []
      this.$emit('handleCancel')
      this.clearSelection()
    }
  }
}
</script>

<style scoped lang="scss" ref="stylesheet/scss">
  .el-table-container{
    .el-pagination-c{
      float: right;
      margin-top: 1.5em;
    }
    .el-row-select{
      margin-top: 6em;
      .el-col-select{
        text-align: center;
      }
    }
  }
</style>
